import { StatusBar } from 'expo-status-bar'
import React, { useEffect, useState } from 'react'
import { SafeAreaProvider } from 'react-native-safe-area-context'
import AppLoading from 'expo-app-loading'
import {
  useFonts,
  Roboto_300Light,
  Roboto_400Regular,
  Roboto_500Medium,
} from '@expo-google-fonts/roboto'

import useCachedResources from './hooks/useCachedResources'
import Navigation from './navigation'
import { register } from './workers/serviceWorkerRegistration'
import { InstallPrompt } from './components/InstallPrompt'
import InstallScreen from './screens/InstallScreen'
import { UserProvider } from './context'
import { isProduction } from './constants/Config'

register()

export default function App() {
  const isLoadingComplete = useCachedResources()
  const [isInstalled, setIsInstalled] = useState(false)
  useEffect(() => {
    const listener = () => setIsInstalled(media.matches)
    const media = window.matchMedia('(display-mode: standalone)')
    /** @note change this to test install screens */
    setIsInstalled(media.matches || !isProduction)
    media.addEventListener('change', listener)
    return () => media.removeEventListener('change', listener)
  }, [])
  let [fontsLoaded] = useFonts({
    Roboto_300Light,
    Roboto_400Regular,
    Roboto_500Medium,
  })

  if (!isLoadingComplete || !fontsLoaded) {
    return <AppLoading />
  } else {
    return (
      <UserProvider>
        <SafeAreaProvider>
          <InstallPrompt />
          {isInstalled ? <Navigation /> : <InstallScreen />}
          <StatusBar />
        </SafeAreaProvider>
      </UserProvider>
    )
  }
}
