import React, { FC, useState } from 'react'
import { ActivityIndicator, StyleSheet } from 'react-native'
import { Card, Image } from 'react-native-elements'
import Icon from 'react-native-vector-icons/MaterialIcons'
import { Text, View } from '../components/Themed'
import Browser from '../constants/Browser'
import Colors from '../constants/Colors'

export const SuccessCard: FC = () => {
  return (
    <Card containerStyle={styles.card}>
      <Card.Title style={styles.title}>
        <Icon
          name="check-circle-outline"
          size={24}
          style={styles.successIcon}
        />
        <Text>Sikeres Telepítés!</Text>
      </Card.Title>
      <Card.Divider />
      <View>
        <Text style={styles.text}>
          Ön sikeresen telepítette az alkalmazást, kérjük nyissa meg azt, majd
          aktiválja felhasználóját!
        </Text>
        <Text style={styles.text}>
          Amennyiben további kérdése maradt lépjen kapcsolatba
          ügyfélszolgálatunkkal!
        </Text>
      </View>
    </Card>
  )
}

export const FailureCard: FC = () => {
  return (
    <Card containerStyle={styles.card}>
      <Card.Title style={styles.title}>
        <Icon name="error-outline" size={24} style={styles.failureIcon} />
        <Text>Sikertelen telepítés!</Text>
      </Card.Title>
      <Card.Divider />
      <View>
        <Text style={styles.text}>
          Úgy tűnik ön akadályba ütközött az alkalmazás telepítése során. Ennek
          több is oka lehet:
        </Text>
        <View style={styles.list}>
          <Text
            style={styles.listItem}
          >{`\u2022 Az alkalmazás már telepítve van az ön eszközén`}</Text>
          <Text
            style={styles.listItem}
          >{`\u2022 Az ön böngészője nem támogatja az alkalmazás letöltését`}</Text>
        </View>
        <Text style={styles.text}>
          Amennyiben további kérdése maradt lépjen kapcsolatba
          ügyfélszolgálatunkkal!
        </Text>
      </View>
    </Card>
  )
}

export const WaitCard: FC = () => {
  return (
    <Card containerStyle={styles.card}>
      <Card.Title style={styles.title}>
        <ActivityIndicator
          color={Colors.orangeWeb}
          style={styles.warningIcon}
        />
        <Text>Kérjük várjon...</Text>
      </Card.Title>
      <Card.Divider />
      <View>
        <Text style={styles.text}>
          A telepítési képernyő pillanatokon belül meg kell, hogy jelenjen.
        </Text>
        <Text style={styles.text}>
          Amennyiben további kérdése maradt lépjen kapcsolatba
          ügyfélszolgálatunkkal!
        </Text>
      </View>
    </Card>
  )
}

export const SafariHowToCard: FC = () => {
  return (
    <Card containerStyle={styles.card}>
      <Card.Title style={styles.title}>
        <Icon name="warning" size={24} style={styles.warningIcon} />
        <Text>Útmutató</Text>
      </Card.Title>
      <Card.Divider />
      <View>
        <Text style={styles.text}>
          Úgy tűnik ön iOS környezetből nyitotta meg rendszerünket, így az
          alkalmazás csak manuálisan telepíthető.
        </Text>
        <View style={styles.list}>
          <Text style={styles.listItem}>
            {`\u2022 Kattintson a "Share"  `}
            <Image
              placeholderStyle={styles.imagePlaceholder}
              containerStyle={styles.imageContainer}
              style={styles.image}
              resizeMode={'stretch'}
              source={{
                uri: `data:image/svg+xml;base64,`,
              }}
            />
            {` gombra böngészője ${Browser.whereIsShare}`}
          </Text>
          <Text style={styles.listItem}>
            {`\u2022 Görgessen balra (amennyiben szükséges) amíg meg nem találja az "Add to Home Screen" `}
            <Image
              placeholderStyle={styles.imagePlaceholder}
              containerStyle={styles.imageContainer}
              style={styles.image}
              resizeMode={'stretch'}
              source={{
                uri: `data:image/svg+xml;base64,`,
              }}
            />
            {` gombot`}
          </Text>
        </View>
        <Text style={styles.text}>
          Amennyiben további kérdése maradt lépjen kapcsolatba
          ügyfélszolgálatunkkal!
        </Text>
      </View>
    </Card>
  )
}

export default function InstallScreen() {
  const [isInstalled, setIsInstalled] = useState(false)
  window.addEventListener('appinstalled', () => {
    setIsInstalled(true)
  })
  return (
    <View style={styles.container}>
      {(() => {
        if (Browser.shouldShowIOSSafariHow2()) {
          return <SafariHowToCard />
        } else if (isInstalled) {
          return <SuccessCard />
        } else if (Browser.shouldShowError()) {
          return <FailureCard />
        } else {
          return <WaitCard />
        }
      })()}
    </View>
  )
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    padding: 20,
    backgroundColor: 'gray',
  },
  fullView: { flex: 1, justifyContent: 'center', alignItems: 'center' },
  card: {
    width: '80%',
    borderRadius: 20,
  },
  title: {
    fontSize: 20,
    fontWeight: 'bold',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  text: {
    fontSize: 14,
    color: 'gray',
    marginBottom: 5,
  },
  list: {
    marginVertical: 10,
  },
  listItem: {
    marginBottom: 3,
    marginLeft: 10,
    fontSize: 14,
    color: 'gray',
  },
  successIcon: {
    color: '#7DE24E',
    marginRight: 5,
  },
  failureIcon: {
    color: '#FF0000',
    marginRight: 5,
  },
  warningIcon: {
    color: Colors.orangeWeb,
    marginRight: 5,
  },
  imagePlaceholder: { backgroundColor: 'transparent' },
  imageContainer: {},
  image: { height: 16, width: 16 },
})
